var a = require(`@/assets/images/marker.png`);

import {
  Style,
  Fill,
  Stroke,
  CircleStyle,
  Icon,
} from "@/wrappers/openLayerImports";
const selectedLayerStyle = new Style({
  stroke: new Stroke({
    color: "rgba(200,20,20,1)",
    width: 1,
  }),
  fill: new Fill({
    color: "rgba(200,20,20,0.4)",
  }),
});
const featureStyle = new Style({
  fill: new Fill({ color: "#00000000" }),
  stroke: new Stroke({ color: "#C672F5", width: 2 }),
  image: new CircleStyle({
    radius: 7,
    fill: new Fill({ color: "#00000000" }),
  }),
});
const markerStyle = new Style({
  image: new Icon({
    anchor: [0.5, 512],
    size: [512, 512],
    anchorXUnits: "fraction",
    anchorYUnits: "pixels",
    src: require(`@/assets/images/marker.png`),
    scale: 0.1,
    // src:
    //   "data:image/png;base64,",
  }),
});
const textPointStyle = new Style({
  fill: new Fill({ color: "#00000000" }),
  stroke: new Stroke({ color: "#00000000", width: 2 }),
});

export { markerStyle, featureStyle, textPointStyle, selectedLayerStyle };
